.main {
    max-width: 1350px;
    margin: 0 auto 199px;
    display:  flex;
    justify-content: space-between;
}
@media screen and (max-width: 1370px) {
    .main {
        margin-bottom: 46px;
        flex-direction: column;
        align-items: center;
    }
}
@media screen and (max-width: 800px) {
    .main {
        margin-bottom: 78px;
    }
}